import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhSaleLazadaInvoiceAdd, VhSaleLazadaInvoiceDetail, VhSaleLazadaInvoiceDetailCombo } from 'src/app/sales/components/bill-type/bill-type-1/lazada/lazada-interface';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhType } from 'src/app/sales/interface/vh-type';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-issue-delivery-goods',
  templateUrl: './issue-delivery-goods.component.html',
  styleUrls: ['./issue-delivery-goods.component.scss']
})
export class IssueDeliveryGoodsComponent implements OnInit {
  searchValue: any;
  searchProductAppValue: any = '';
  salesOrder: Array<any> = [];
  salesSubOrder: Array<any> = [];
  heightScroll: any;
  public date: Date[] = [new Date(), new Date()];
  load_status: boolean = false;
  dataRestore: any = {};
  bill_selected: any = {};
  /**Gii hn thi gian cho xem bill */
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < this.minDate.getTime() - 24 * 60 * 60 * 1000) return true;
    return false;
  };
  minDate: Date = new Date(Date.now() - 24 * 60 * 60 * 1000 * 30);
  infoShop: any = {};
  listProductOfBranch: Array<any> = []; // sp ca phn mm c lin kt vi lazada
  listProductApp: Array<any> = []; //sp ca phn mm
  listSubProductApp: Array<any> = []; //sp ca app cha lazada[]
  listProductLazada: Array<any> = [];
  id_branch: string = this.vhQuerySales.getDefaultBranch()._id;
  setHeight = { "max-height": "500px", "height": "500px", "overflow-y": "hidden" };
  arrayHidden: Array<any> = []; //mng ny cha id ca nhng sp ang tm n

  invoice: VhSaleLazadaInvoiceAdd = {
    bill_type: 1,
    cqt_status: 1,
    payment_type: 2,
    id_customer: '',
    id_branch: '',
    id_employee: '',
    id_wallet: 'id_cash',
    date: '',
    total: 0,
    payment: 0,
    tax: 0,
    discount: 0,
    fee: 0,
    note: '',
    price_type: 0,
    order_number_ecommerce: '',
    id_shop: '',
    tax_buyerDisplayName: '',
    tax_buyerLegalName: '',
    tax_buyerTaxCode: '',
    tax_buyerAddressLine: '',
    tax_buyerEmail: ''
  };
  invoiceDetail = [];
  employee: any = {};
  customer: any = {};
  subTotal = 0;

  constructor(
    private router: Router,
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private cdRef: ChangeDetectorRef,
    private nzModalService: NzModalService,
    private vhAuth: VhAuth
  ) {
    this.date[0].setHours(0, 0, 0, 0);
    this.date[1].setHours(23, 59, 59, 59);
    this.dataRestore = this.router.getCurrentNavigation().extras.state?.dataRestore || {};
  }

  ngOnInit(): void {
    this.initState();

  }


  initState() {
    if (this.dataRestore.id_shop_selected) {
      this.date = this.dataRestore.date;
      this.searchValue = this.dataRestore.searchValue;
      this.salesOrder = this.dataRestore.salesOrder;
      this.salesSubOrder = this.salesOrder;
      if (this.dataRestore['update']) {
        this.dataRestore['update'] = false;
        this.showReport();
      }
      else
        this.load_status = true;
    }
    this.infoShop = this.vhQuerySales.getlocalShopListLazada()[0];
    this.getMinDate();

    this.listProductOfBranch = this.vhQuerySales.getlocalDetailProducts(35).filter(e => !e.delete_hidden).filter(item => item[this.id_branch] && item[this.id_branch].lazadas && item[this.id_branch].lazadas.length >= 0);
    let localProducts: Array<any> = this.vhQuerySales.getlocalDetailProducts(35).filter(e => !e.delete_hidden);

    this.listProductApp = localProducts.filter(e => !e.delete_hidden).map(item => {
      if (item[this.id_branch] && item[this.id_branch].lazadas && item[this.id_branch].lazadas.length) { //filter nhng sp lazada thuc id lazada  chn
        item[this.id_branch].lazadas = [...item[this.id_branch].lazadas.filter(prod => prod.id_shop == this.infoShop._id)];
      }
      return { ...item, ...this.vhQuerySales.getUnit_byRatio(item.units, 1) };
    });
    this.listSubProductApp = [...this.listProductApp];
    this.returnArrayHiddenProduct();
    this.listProductLazada = this.clearProductShow(this.vhQuerySales.getlocalProductListLazada(this.infoShop['_id']).filter(item => {
      if (item && !this.arrayHidden.includes(item.item_id)) return item;
    }), this.listProductApp);

    console.log('listProductLazada', this.listProductLazada);
    this.listProductLazada.forEach(item => item.name = item.attributes.name);
  }

  /**
  * Ly ra nhng id ca cc sn phm b tm n
  * @example this.returnArrayHiddenProduct()
  */
  returnArrayHiddenProduct() {
    let arr: Array<any> = [];
    this.arrayHidden = [];
    let localProduct: Array<any> = this.vhQuerySales.getlocalDetailProducts(35).filter(e => !e.delete_hidden);
    for (let product of localProduct) {
      if (product[this.id_branch] && product[this.id_branch].lazadas && product[this.id_branch].lazadas.length) {
        for (let prod of product[this.id_branch].lazadas)
          if (prod.hidden == true && prod.id_shop == this.infoShop['_id']) arr.push(prod.item_id);
      }
    }
    this.arrayHidden = arr;
  }



  /**
   * Ly ngy gii hn c xem bo co
   * @example this.getMinDate()
  */
  getMinDate() {
    let date = new Date(Date.now() - 24 * 60 * 60 * 1000 * 100);
    this.vhQuerySales.getValidityDate(date).then((validitydate: Date) => {
      this.minDate = validitydate;
      if ((this.minDate.getTime()) < new Date(this.vhAuth.getStore().registrationdate).getTime()) {
        this.disabledDate = (current: Date): boolean => {
          if (current.getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000) return true;
          return false;
        };
      }
    });
  }


  /**Tm kim ha n */
  search(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase());
      this.salesOrder = this.vhAlgorithm.searchList(val, this.salesSubOrder, ['order_id']);
    } else this.salesOrder = [...this.salesSubOrder];
  }

  /**
   * search tn sn phm trong app viethas
   * @example this.search('Sn phm A')
   */
  searchProductApp(value: string) {
    let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase());
    this.listProductApp = this.vhAlgorithm.searchList(val, [...this.listSubProductApp],  ['name','barcode','item_code']);
  }

  /**
   * v trang qun l pht hnh ha n
   * @example this.goBack()
   */
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/lazada/issue-invoice'], {
      state: { dataRestore: this.dataRestore }
    });
  }


  /**
   * bt s kin thay i ngy xem bo co
   * @example this.changeTime('2023-06-09T04:14:30.367Z')
   */
  changeTime(event) {

    if (event[1].getTime() - event[0].getTime() <= 2678400000) {
    } else {
      this.vhComponent.showToast(2000, this.lang.translate("The time to view no more than 30 days"), "alert-toast");
      this.date = [new Date(event[0]), new Date(event[0].getTime() + 2678313600)];
    }
  }


  /**
   * get ha n lazada
   * @example this.showReport()
   */
  showReport() {
    this.load_status = true;
    this.salesOrder = [];
    this.salesSubOrder = [];
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhQuerySales.getSalesOrderDetailListLazada(new Date(this.date[0].setHours(0, 0, 0)).toISOString(), new Date(this.date[1].setHours(23, 59, 59, 59)).toISOString(), this.infoShop.access_token, this.infoShop._id)
        .then((result: Array<any>) => {
          result.forEach(item => {
            if (item.statuses.length > 1) {
              let count = 0;
              item.order_items.forEach(product => { if (product.status == 'canceled') count++; });
              item.deliveredProduct = item.order_items.length - count;
              item.canceledProduct = count;
            }
            if (item.verify_status_delivered_goods) this.salesOrder.push({ id_shop: this.infoShop._id, ...item, total_order: this.calculateTotal(item) });
          });
          this.salesSubOrder = [...this.salesOrder];
          this.vhComponent.hideLoading(0);
        }).catch(err => this.vhComponent.hideLoading(0));
    });
  }


  /**
   * tnh li tng tin
   * @param bill
   * @returns number
   * @example let total = this.calculateTotal(bill)
   */
  calculateTotal(bill) {
    return +bill['price'] - bill['voucher_platform'] - bill['voucher_seller'] + bill['shipping_fee'];
  }
  changeShop(event) { }
  ngAfterViewChecked() {
    if (document.querySelector("#lazada-issue-invoice") && document.querySelector(".ant-table-thead") && document.querySelector(".lazada-issue-invoice-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#lazada-issue-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".lazada-issue-invoice-header").clientHeight - 90 + "px";
    }

    this.cdRef.detectChanges();
  }
  /**
   * router qua trang pht hnh ha n
   * @param bill
   */
  gotoCreateBill(bill: any) {


    //tm customer  loi tr bug khch hng b xa ri
    let customer: any = this.vhQuerySales.getlocalCustomer(this.infoShop.data?.id_customer);
    if (customer) {
      this.router.navigate(['/sales/dashboard/manage/ecommerce/lazada/issue-invoice/issue-delivery-goods/add'], {
        state: { dataRestore: { ...this.dataRestore, searchValue: this.searchValue, id_shop_selected: this.infoShop._id, date: this.date, salesOrder: this.salesOrder }, billLazada: bill }
      });
    } else this.vhComponent.alertMessageDesktop('warning', this.lang.translate('Please update customer in settings'));
  }
  /**
   * router qua trang xem chi tit ha n  pht hnh
   * @param bill
   */
  gotoDetail(bill: any) {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/lazada/issue-invoice/issue-delivery-goods/detail'], {
      state: { dataRestore: { ...this.dataRestore, searchValue: this.searchValue, id_shop_selected: this.infoShop._id, date: this.date, salesOrder: this.salesOrder }, id: bill.id_bill_delivered_goods, billLazada: bill }
    });
  }

  /**
   * tr v tn trng thi ha n
   * @param status
   * @returns string
   * @example let status_name = this.renderStatus(2)
   */
  renderStatus(status) {
    switch (status) {

      case 2: case 3: return this.lang.translate('Wait for confirmation');
      case 6: return this.lang.translate('Delivering');
      case 7: return this.lang.translate('Delivered');
      case 11: case 12: case 14: case 15: case 19: return this.lang.translate('Trouble');
      case 13: return this.lang.translate("Cancelled");
      case 8: return this.lang.translate("Completed");
      case 10: return this.lang.translate("ng");
      case 21: return this.lang.translate("ang i tr");
      case 22: return this.lang.translate("i tr thnh cng");
      case 23: return this.lang.translate("Ch Lazada x l");
    }
  }

  /**
   * xem chi tit n t hng ca lazada
   * @param bill
   * @example this.gotoDetailLazada(bill)
   */
  gotoDetailLazada(bill) {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/lazada/issue-invoice/issue-delivery-goods/detail-order'],
      {
        state: {
          bill: bill,
          dataRestore: {
            salesOrder: this.salesOrder, searchValue: this.searchValue,
            id_shop_selected: this.infoShop._id, listProduct: this.dataRestore.listProduct,
            date: this.date,
            salesSubOrder: this.salesSubOrder,
          }
        }
      });
  }

  private groupedItemsMap = new Map<string, any[]>();

  getGroupedItemsCached(orderItems: any[]): any[] {
    const key = JSON.stringify(orderItems);
    if (!this.groupedItemsMap.has(key)) {
      const filtered = orderItems.filter(e => !e.reason || e.status == 'delivered');
      const grouped = new Map<number, { sku_id: number; name: string; quantity: number; }>();
      for (const item of filtered) {
        if (grouped.has(item.sku_id)) {
          grouped.get(item.sku_id)!.quantity += 1;
        } else {
          grouped.set(item.sku_id, {
            sku_id: Number(item.sku_id),
            name: item.name,
            quantity: 1
          });
        }
      }
      this.groupedItemsMap.set(key, Array.from(grouped.values()));
    }

    return this.groupedItemsMap.get(key)!;
  }

  findProductLinked(item) {
    for (let productOfBranch of this.listProductOfBranch) {
      for (let productLazada of productOfBranch[this.id_branch].lazadas) {
        let findProd = productLazada.skus.find(sku => sku.SkuId == item.sku_id);
        if (findProd) {
          return productOfBranch;
        }
      }
    }

    return null;
  }

  /**
   * Kim tra sn phm  c lien kt hay cha
   *
   */
  isLinkedToProduct(item): boolean {
    for (let productOfBranch of this.listProductOfBranch) {
      for (let productLazada of productOfBranch[this.id_branch].lazadas) {
        let findProd = productLazada.skus.find(sku => sku.SkuId == item.sku_id);
        if (findProd) {
          return true;
        }
      }
    };
    return false;
  }

  visible_affiliate: boolean = false; // hin th modal chn sn phm phn mm lin kt
  productLazadaSelected: any = null; // sn phm lazada c chn  lien kt


  openModelSelectProductsLazada(productLazada) {
    this.productLazadaSelected = productLazada;
    this.visible_affiliate = true;
  }


  /**
   * Tt modal v clone mng product ra mt mng mi  trnh nh li product c b checked
   */
  cancelAffiliate() {
    this.visible_affiliate = false;
  }


  /**
    * Lin kt sn phm t lazada vi sn phm ti ca hng.
    * @param product Sn phm ca ca hng
    */
  acceptAfiliate(product) {
    const isZeroPriceCombo = product.type === VhType.COMBO && product.units[0].price === 0;
    const confirmZeroPriceComboMessage = this.lang.translate("This product is a combo with a price of 0. Do you still want to link it with the product", { platform: 'Lazada' }) + "?";
    this.nzModalService.confirm({
      nzTitle: '<i>' + (isZeroPriceCombo ? confirmZeroPriceComboMessage : this.lang.translate("Confirm")) + '</i>',
      nzIconType: "info-circle",
      nzCancelText: this.lang.translate("Cancel"),
      nzOnOk: () => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          // Tm sn phm lazada theo sku_id
          const selectedSkuId = this.productLazadaSelected?.sku_id;
          const matchedProduct = this.listProductLazada.find(product =>
            product.skus?.some(sku => sku.SkuId == selectedSkuId)
          );

          const id_product_selected = product.id_subproduct ? product.id_subproduct : product['_id'];

          if (!matchedProduct) {
            this.vhComponent.hideLoading().then(() => {
              this.vhComponent.alertMessageDesktop('error', this.lang.translate("The product link failed"));
            });
            return;
          }

          let productLazada = {
            id_shop: this.infoShop._id,
            item_id: matchedProduct.item_id,
            SkuIds: [{ SkuId: this.productLazadaSelected.sku_id }]
          };

          this.vhQuerySales.addMergeLazadasProduct_skus(id_product_selected, productLazada.item_id, productLazada.SkuIds, this.infoShop['_id']).then((bool) => {
            this.vhComponent.hideLoading().then(() => {
              setTimeout(() => {
                this.visible_affiliate = false;
                this.listProductOfBranch = this.vhQuerySales.getlocalDetailProducts(35).filter(e => !e.delete_hidden).filter(item => item[this.id_branch] && item[this.id_branch].lazadas && item[this.id_branch].lazadas.length >= 0);
                this.vhComponent.alertMessageDesktop('success', this.lang.translate("Product linked successfully"));
              }, 300);
            });
          }).catch(err => {
            console.error(err);
            this.visible_affiliate = false;
            this.vhComponent.alertMessageDesktop('error', this.lang.translate("The product link failed"));
            this.vhComponent.hideLoading(0);
          });
        });
      }
    });
  }

  /**Xa nhng sp  c lin kt ri ra khi mng ca modal danh sch sp lin kt
   * @param listProduct : danh sch sp  lk vi sn
   * @param listProductApp : ton b danh sch sp ca app
   * @example let daa =  this.clearProductShow([], [])
   *
  */
  clearProductShow(listProduct, listProductApp) {
    for (let prodApp of listProductApp) {
      if (prodApp[this.id_branch] && prodApp[this.id_branch].lazadas && prodApp[this.id_branch].lazadas.length) {
        for (let item of prodApp[this.id_branch].lazadas) {
          let indexProd = listProduct.findIndex(prod => prod.item_id == item.item_id);
          if (indexProd >= 0) {
            for (let indexSku in item.skus) {
              let indexSub = listProduct[indexProd].skus.findIndex(sku => sku.SkuId == item.skus[indexSku].SkuId);
              if (indexSub >= 0) {
                listProduct[indexProd].skus.splice(indexSub, 1);
                if (listProduct[indexProd].skus.length == 0) listProduct.splice(indexProd, 1);
              }
            }
          }
        }
      }
    }
    return listProduct;
  }

  /* ----------- THANH TON HA N HNG LOT ------ */


  checkAll: boolean = false;
  selectedBills: { [id: string]: boolean; } = {};
  listInvoiceSelected = [];

  toggleSelectAll(checked: boolean) {
    this.checkAll = checked;
    this.listInvoiceSelected = []; // reset before adding

    for (let bill of this.salesOrder) {
      const canSelect = !bill.id_bill_delivered_goods && this.billIsLinkedToProduct(bill);

      if (canSelect) {
        this.selectedBills[bill.order_id] = checked;

        if (checked) {
          this.listInvoiceSelected.push(bill);
        }
      } else {
        // ensure unselect disabled ones
        this.selectedBills[bill.order_id] = false;
      }
    }
  }


  billIsLinkedToProduct(bill) {
    const order_items = bill.order_items.filter(e => !e.reason || e.status == "delivered");
    return order_items.length > 0 && order_items.every(item => this.isLinkedToProduct(item));
  }

  checkBillToCreateInvoice(bool, bill) {
    this.selectedBills[bill.order_id] = bool;
    if (bool) {
      this.listInvoiceSelected.push(bill);
    }
    else {
      this.listInvoiceSelected = this.listInvoiceSelected.filter(item => item.order_id != bill.order_id);
    }

    const allBillsCanSelect = this.salesOrder.filter(
      b => !b.id_bill_delivered_goods && this.billIsLinkedToProduct(b)
    );

    this.checkAll = allBillsCanSelect.every(b => this.selectedBills[b.order_id]);
  }

  payInvoice() {
    this.vhComponent.alertConfirm(
      `${this.lang.translate("Pay")}?`,
      "", "", "OK", this.lang.translate("Cancel")
    )
      .then(() => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          const updatePromises = [];

          for (let index = 0; index < this.listInvoiceSelected.length; index++) {
            const billLazada = this.listInvoiceSelected[index];
            this.initBill(billLazada);


            const promise = this.createOrUpdate(1).then((bill: any) => {
              this.salesOrder.find(item => item.order_id == billLazada['order_id']).id_bill_delivered_goods = bill._id;
              if (this.invoice.cqt_status !== 1) {
                this.vhQuerySales.updateBill(bill._id, { cqt_status: this.invoice.cqt_status });
              }
              return this.vhQuerySales.updateSalesOrderLazada(this.infoShop._id, billLazada['order_id'].toString(), { id_bill_delivered_goods: bill._id });
            });

            updatePromises.push(promise.catch(err => {
              this.vhComponent.showToast(2000, err);
              return null;
            }));
          }

          // Wait for all promises to resolve (or reject caught above)
          Promise.all(updatePromises).then(() => {
            this.vhComponent.hideLoading(0).then(() => {
              this.vhComponent.showToast(2000,
                `${this.lang.translate("Issue invoice")} ${this.lang.translate("Successfully").toLocaleLowerCase()}`, "success-toast"
              );
            });
          });
        });
      }, () => { });

    // }
    // else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  createOrUpdate(bill_type: 1) {
    return new Promise((resolve, reject) => {
      this.vhQuerySales.createBill_Billdetail({ ...this.invoice }, this.getCreateUpdateInvoiceDetail()).then(res => resolve(res)).catch(err => reject(err));
    });
  }

  /**
     * Thm sn phm  bn vo cart
     * Cng thc tnh free ship max = 4,5% * (gi tr sp - gi tr NBH ti tr) * 1.1 (ti a 16.5k trn 1sp)
     * Ph thanh ton = 2.002% x (gi sn phm  chi ph nh bn hng ti tr + ph vn chuyn tr bi khch hng)
     * @example this.addInvoiceDetail(array_order_item_lazada)
     */
  addInvoiceDetail(billLazada: any) {
    let order_items: Array<any> = billLazada.order_items.filter(e => !e.reason || e.status == "delivered");
    let freeShipMax: number = 0;
    let paymet_fee: number = 0;
    let products: Array<any> = this.vhQuerySales.getlocalDetailProducts(35).filter(e => !e.delete_hidden).filter(item => item[this.id_branch] && item[this.id_branch].lazadas && item[this.id_branch].lazadas.length >= 0);


    for (let order_item of order_items) {
      paymet_fee += parseFloat((0.02002 * (order_item.item_price - order_item.voucher_seller + order_item.shipping_amount)).toFixed(2));
      freeShipMax += parseFloat((0.045 * (order_item.item_price - order_item.voucher_seller) * 1.1).toFixed(2));

      let findSuccess: boolean = false;
      for (let productApp of products) {
        for (let productLazada of productApp[this.id_branch].lazadas) {
          let findProd = productLazada.skus.find(sku => sku.SkuId == order_item.sku_id);
          if (findProd) {
            findSuccess = true;
            this.addInvoiceDetailLazada(order_item.item_price, order_item.paid_price, productApp, true);
            break;
          }
        }
        if (findSuccess) break;
      }
      if (!findSuccess) {
        this.addInvoiceDetailLazada(order_item.item_price, order_item.paid_price, { name: order_item.name }, false);
      }

    }
    let fee = parseFloat(freeShipMax.toFixed(2));
    this.invoice.discount = ((fee > 16500 ? 16500 : fee) + parseFloat(paymet_fee.toFixed(2)) + billLazada.shipping_fee_discount_seller);
  }


  /**
   *
   * @param price
   * @param product
   * @param exist
   * @example addInvoiceDetailLazada(price_on_lazada, product, false)
   */
  addInvoiceDetailLazada(price, price_origin, product, exist) {
    let index = this.invoiceDetail.findIndex(detail => detail['id_product'] == product['_id'] && product['_id'] != null);
    if (index != -1) {
      if (this.invoiceDetail[index]['quantity'] < 9999999999) this.invoiceDetail[index]['quantity'] += 1;
    }
    else {
      if (exist) {
        const productToAdd = {
          name: product.name,
          id_product: product._id,
          quantity: 1,
          ptype: product.type,
          unit: product.unit,
          ratio: 1,
          units: product.units,
          price: price,
          price_origin: price_origin,
          id_subproduct: product.id_subproduct,
          lots: product.lots,
          id_lotproduct: product.id_lotproduct,
          free: false //nu c trng ny true th c quyn chnh s lng
        }
        if(product.type == VhType.COMBO) {
          productToAdd['combos'] = product.combos;
        }
        this.invoiceDetail.unshift(productToAdd);
      } else {
        this.invoiceDetail.unshift({
          id_product: null,
          quantity: 1,
          price: price,
          ptype: 1,

          ratio: 1,
          name: product.name
        });
      }
    }
  }

  /**
     * tr v danh sch bill_detail  format theo ng chun cc trng  lu DB
     * @returns array bill_detail
     * @example let bill_details = getCreateUpdateInvoiceDetail()
     */
  getCreateUpdateInvoiceDetail(): VhSaleLazadaInvoiceDetail[] {
    return this.invoiceDetail.map(item => {

      let detail: VhSaleLazadaInvoiceDetail = {
        id_product: item.id_product,
        ptype: item.ptype,
        price: item.price,
        quantity: item.quantity,
        ratio: item.ratio,
        price_origin: item.price_origin,
      };
      if (item.id_lotproduct) detail['id_lotproduct'] = item.id_lotproduct;
      if (item.id_subproduct) detail['id_subproduct'] = item.id_subproduct;

      if (item.ptype == 5) {
        detail['combos'] = item.combos.map(combo => {
          let product: VhSaleLazadaInvoiceDetailCombo = {
            id_product: combo.id_product,
            price: combo.price,
            quantity: combo.quantity,
            ptype: combo.ptype || combo.type,
            ratio: combo.ratio ? combo.ratio : 1,
          };
          if (combo.id_lotproduct) product['id_lotproduct'] = combo.id_lotproduct;
          if (combo.id_subproduct) product['id_subproduct'] = combo.id_subproduct;
          return product;
        });
      }
      return detail;

    }).filter(item => item);

  }
  pickerTime = new Date();

  initBill(billLazada) {
    this.resetInvoice();

    this.invoice.date = this.pickerTime.toISOString();
    this.invoice.id_employee = this.vhAuth.getUser()._id;
    this.employee = this.vhAuth.getlocalEmployee(this.invoice.id_employee);
    this.invoice.id_branch = (this.vhQuerySales.getDefaultBranch()._id);
    this.invoice.id_customer = (this.vhQuerySales.getlocalShopListLazada().find(item => item._id == this.infoShop._id).data.id_customer);
    this.customer = this.vhQuerySales.getlocalCustomer(this.invoice.id_customer);
    this.invoice.payment_type = (2); //set bill cng n
    this.addInvoiceDetail(billLazada);
    this.setSubTotal();
    this.invoice.fee = 0;
    this.invoice.order_number_ecommerce = (billLazada['order_id'].toString());
    this.invoice.id_shop = (this.infoShop._id);
    this.invoice.note = (`${this.lang.translate("Order number")} Lazada ${billLazada.order_id}`);
    this.invoice.tax_buyerDisplayName = this.invoice.tax_buyerDisplayName ? this.invoice.tax_buyerDisplayName : 'Ngi mua khng ly ha n';
    this.invoice.cqt_status = billLazada.cqt_status ? billLazada.cqt_status : 1;
  }

  /**
   * tnh v gn li cng tin (subTotal) ca n
   * @example this.setSubTotal()
   */
  setSubTotal() {
    let subTotal = 0;
    for (let i of this.invoiceDetail) {
      subTotal += (i.price ? i.price * i.quantity : 0);
    }
    this.subTotal = subTotal;
    this.setTotal();
  }

  /**
   * tnh v gn li tng tin (total) ca n
   * @example this.setTotal()
   */
  setTotal() {
    this.invoice.total = this.subTotal - this.invoice.discount + this.invoice.fee > 0 ? this.subTotal - this.invoice.discount + this.invoice.fee : 0;
  }

  resetInvoice() {
    this.invoice = {
      bill_type: 1,
      cqt_status: 1,
      payment_type: 2,
      id_customer: '',
      id_branch: '',
      id_employee: '',
      id_wallet: 'id_cash',
      date: '',
      total: 0,
      payment: 0,
      tax: 0,
      discount: 0,
      fee: 0,
      note: '',
      price_type: 0,
      order_number_ecommerce: '',
      id_shop: '',
      tax_buyerDisplayName: '',
      tax_buyerLegalName: '',
      tax_buyerTaxCode: '',
      tax_buyerAddressLine: '',
      tax_buyerEmail: ''
    };
    this.invoiceDetail = [];
  }
  
  
  
  /**
   * m s lng bill ang check
   */
  get countSelectedBill(): number {
    let count = 0;
    for (let key in this.selectedBills) {
      if (this.selectedBills[key]) {
        count++;
      }
    }
    return count;
  }

}
