import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IssueDeliveryGoodsComponent } from './issue-delivery-goods.component';
import { AddComponent } from './add/add.component';
import { DetailComponent } from './detail/detail.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { FormsModule } from '@angular/forms';
import { DetailOrderComponent } from './detail-order/detail.component';
import { EditComponent } from './edit/edit.component';
import { BillType1LazadaModule } from 'src/app/sales/components/bill-type/bill-type-1/lazada/bill-type1-lazada.module';
import { ModalLinkProductEcommerceModule } from 'src/app/sales/menu/manage/ecommerce/components/modal-link-product-ecommerce/modal-link-product-ecommerce.module';

const routes: Routes = [
  {
    path: "",
    component: IssueDeliveryGoodsComponent
  },
  {
    path: "add",
    component: AddComponent
  },
  {
    path: "edit",
    component: EditComponent
  },
  {
    path: "detail",
    component: DetailComponent
  },
  {
    path: "detail-order",
    component: DetailOrderComponent
  },
]

@NgModule({
  declarations: [IssueDeliveryGoodsComponent, AddComponent, DetailComponent, DetailOrderComponent, EditComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    BillType1LazadaModule,
    ModalLinkProductEcommerceModule
  ]
})
export class IssueDeliveryGoodsModule { }
